<template>
<div class="card-group container-position col-12">
    <div class="row">
  <div v-for="group in floor_group_maps" :key="group.id" class="col-6">
  <div @click="seeDetails(group)" class="card">
      <div class="card-body">
        <h5 class="card-title">{{group.name}}</h5>
        <p class="card-text">{{group.floor_maps.length}} keys</p>
       </div>
    <img class="card-img-top" src="@/assets/img/level2.png" alt="Card image cap">
  </div>
    </div>
</div>
</div>
</template>

<script>
import mapRepository from "@/repository/map";
import lodash from "lodash";
import store from "@/flulux/store";
export default {
  data() {
    return {
      floor_group_maps: [],
      floor_maps: [],
      groupsWithMaps: {},
      store: store
    };
  },
  async mounted() {
    await this.getAllGroupFloorMaps();
    await this.getAllFloorMaps();
    this.getAllFloorGroupMapsWithFloorMapsAndPictures()
    this.mapGroupsWithMaps();
  },
  methods: {
    seeDetails(p) {
      this.store.state.map.currentGroup = p;
      this.$router.push("Keychain-map-details");
    },
    async getAllGroupFloorMaps() {
      this.floor_group_maps = await mapRepository.getAllFloorGroupMaps();
    },
    async getAllFloorMaps() {
      this.floor_maps = await mapRepository.getAllFloorMaps();
    },
    async getAllFloorGroupMapsWithFloorMapsAndPictures() {
      this.groupsWithMaps = await mapRepository.getAllFloorGroupMapsWithFloorMapsAndPictures();
    },
    mapGroupsWithMaps() {
      const groupsWithMaps = this.floor_group_maps.map(item => ({
        floor_maps: lodash.filter(this.floor_maps, {
          floor_map_group_id: item.id
        }),
        ...item
      }));
      this.floor_group_maps = [...groupsWithMaps];
    }
  }
};
</script>

